import * as codeApi from '@/pages/databench/api/code'
import {
  FileCfg,
  IBizData,
  IFileTreeData,
  ICommitRecord,
  IFile,
  IFileContent,
  IFileDetail,
  IWorkspaceData
} from '@/pages/databench/types/server'
import { message } from 'antd'
import { action, observable, runInAction, toJS } from 'mobx'

const workspaceData: IWorkspaceData = observable<IWorkspaceData>({
  id: 0,
  name: '测试空间',
  bizList: [],
  fileList: []
  // bizGraphs: {}
})

workspaceData.id = 1

const fetchBizList = action(async () => {
  const workspaceId = workspaceData.id
  let list: IBizData[] = await codeApi.fetchBizList(workspaceId)
  const promises = list.map((i) => codeApi.fetchTreeData(workspaceId, 'development', i.id))
  return Promise.all(promises).then((items) => {
    items.forEach((bizTreeData, index) => {
      list[index].bizTreeData = bizTreeData
    })
    runInAction(() => {
      workspaceData.bizList = list
    })
  })
})

const fetchFileDetail = action(async (fileId: number) => {
  const fileDetail = await codeApi.fetchFileDetail(fileId)
  const index = workspaceData.fileList.findIndex((i) => i.id === fileId)
  const fileData = {
    ...fileDetail,
    content: { ...fileDetail.content },
    id: fileId
  }
  runInAction(() => {
    if (index === -1) {
      workspaceData.fileList.push(fileData)
    } else {
      workspaceData.fileList[index] = fileData
    }
  })
  return fileDetail
})

const fetchTempQueryTreeData = action(async () => {
  const tempQueryTreeData: IFileTreeData = await codeApi.fetchTreeData(
    workspaceData.id,
    'query',
    -1
  )
  runInAction(() => {
    workspaceData.tmpQueryTreeData = tempQueryTreeData
  })
  return tempQueryTreeData
})

const fetchResourceTreeData = action(async () => {
  const resourceTreeData: IFileTreeData = await codeApi.fetchTreeData(
    workspaceData.id,
    'resource',
    -1
  )
  runInAction(() => {
    workspaceData.resourceTreeData = resourceTreeData
  })
  return resourceTreeData
})

const fetchDeleteFileList = action(async () => {
  const deleteFileList: IFile[] = await codeApi.fetchDeletedFileList(workspaceData.id)
  workspaceData.deleteFileList = deleteFileList
  return deleteFileList
})

