const getFileData = (fileId: number): IFileDetail | undefined =>
  workspaceData.fileList.find((i) => i.id === fileId)

const consumerFileData = (fileId: number, consumer: <T = any>(fileData: IFileDetail) => void) => {
  const file = getFileData(fileId)
  return file && consumer(file)
}

const saveFile = action((fileId: number) => {
  const file = getFileData(fileId)
  return (
    file &&
    codeApi.saveFile(file).then(() => {
      message.success('保存成功!')
    })
  )
})

const fetchCommitRecords = action(async (fileId: number) => {
  const records: ICommitRecord[] = await codeApi.fetchCommitRecords(fileId)
  // getFileData(fileId)?.commitRecords = records
  records.sort((a, b) => b.version - a.version)
  runInAction(() => {
    consumerFileData(fileId, (i) => (i.commitRecords = records))
  })
  return records
})

export default {
  workspaceData,
  fetchBizList,
  fetchFileDetail,
  changeFileContent: action((fileId: number, consumer: (file: IFileContent) => void) => {
    const file = workspaceData.fileList.find((i) => i.id === fileId)
    file && consumer(file.content)
  }),
  changeFileCfg: action((fileId: number, consumer: (file: FileCfg) => void) => {
    const file = workspaceData.fileList.find((i) => i.id === fileId)
    file && consumer(file.cfg)
  }),
  // 只是移除数据非业务逻辑
  // removeFileData: action((fileId: number) => {
  //   const list = workspaceData.fileList
  //   const index = list.findIndex((i) => i.id === fileId)
  //   list.splice(index, 1)
  // }),
  saveFile,
  getFileData,
  fetchTempQueryTreeData,
  fetchResourceTreeData,
  fetchDeleteFileList,

  // fetchBizGraph,
  fetchCommitRecords
}
