import { message } from 'antd'
import axios, { AxiosRequestConfig } from 'axios'

const service = axios.create({
  baseURL: '/api',
  timeout: 60000
})

export interface Result<T = any> {
  msg: string
  data: T
  errorCdoe: number
  success: boolean
}

const requestResult = async <T = any>(config: AxiosRequestConfig): Promise<Result<T>> => {
  try {
    const { data } = await service.request<Result>(config)
    return data
  } catch (error) {
    const msg = (error as any).message || 'Request error'
    const res: Result<any> = {
      errorCdoe: -1,
      msg,
      data: null as any,
      success: false
    }
    return res
  }
}

const requstData = async <T = any>(config: AxiosRequestConfig): Promise<T> => {
  return new Promise<T>((res, rej) => {
    return requestResult(config).then((result) => {
      if (result.success) {
        res(result.data)
      } else {
        // console.error(result.msg)
        message.error(result.msg)
        rej(result.msg)
      }
    })
  })
}

const GET = async <T = any>(url: string): Promise<T> => requstData({ url, method: 'GET' })
const POST = async <T = any>(url: string, data?: any, config?: AxiosRequestConfig): Promise<T> =>
  requstData({ url, method: 'POST', data, ...config })
const PUT = async <T = any>(url: string, data?: any, config?: AxiosRequestConfig): Promise<T> =>
  requstData({ url, method: 'PUT', data, ...config })
const DELETE = async <T = any>(url: string, config?: AxiosRequestConfig): Promise<T> =>
  requstData({ url, method: 'DELETE', ...config })

export default {
  GET,
  POST,
  PUT,
  DELETE
}
