// store.ts
import { action, autorun, computed, has, observable, toJS } from 'mobx'
import { ITab } from '@/pages/databench/types/client'
import { getLocal, setLocal } from '@/utils/common'

export type Theme = 'dark' | 'light'

interface UiSetting {
  theme: Theme
  curTabKey: string
  windowFooterSplitSize: number
  windowFooterVsible: boolean
}

var tabs: ITab[] = observable.array(getLocal('___tabs_caches', []), { deep: true })
var uiSetting: UiSetting = observable(
  getLocal('___ui_setting', {
    theme: 'dark',
    curTabKey: '',
    windowFooterSplitSize: 300,
    windowFooterVsible: false
  })
)
// const tabPageLoaded: { [key: string]: boolean | undefined } = observable({})

const toggleWindowFooterVisible = action(
  () => (uiSetting.windowFooterVsible = !uiSetting.windowFooterVsible)
)

const changeWindowFooterSplitSize = action(
  (size: number) => (uiSetting.windowFooterSplitSize = size)
)

autorun(() => {
  setLocal('___tabs_caches', tabs)
  setLocal('___ui_setting', uiSetting)

  if (uiSetting.theme === 'dark') {
    document.querySelector('html')?.classList.add('dark')
  } else {
    document.querySelector('html')?.classList.remove('dark')
  }
})

const toggleTheme = action(() => {
  uiSetting.theme = uiSetting.theme === 'dark' ? 'light' : 'dark'
})

//  tab
const getTab = (key: string) => tabs.find((i) => i.key === key)
const getTabIndex = (key: string) => tabs.findIndex((i) => i.key === key)

// action
const addTab = action((newTab: ITab) => {
  const { key, tmp } = newTab
  let tab = getTab(key)
  const tmpIndex = tabs.findIndex((i: ITab) => i.tmp)
  if (!tab) {
    if (tmpIndex !== -1) {
      tabs[tmpIndex] = newTab
      changeTab(key)
    } else {
      tabs.splice(getTabIndex(uiSetting.curTabKey) + 1, 0, newTab)
      changeTab(key)
    }
  } else {
    if (uiSetting.curTabKey === key) {
      if (!tmp) {
        tab.tmp = false
      }
    } else {
      changeTab(key)
    }
  }
})

const changeTab = action((key: string) => {
  uiSetting.curTabKey = key
})

const closeTab = action((key: string) => {
  const index = getTabIndex(key)
  if (index !== -1) {
    tabs.splice(index, 1)
    if (key === uiSetting.curTabKey) {
      const nextIndex = index < tabs.length ? index : index - 1
      if (nextIndex !== -1) {
        changeTab(tabs[nextIndex]?.key)
      }
    }
  }
})

const closeAllTabs = action(() => {
  tabs.splice(0, tabs.length)
  uiSetting.curTabKey = ''
})

const closeOtherTabs = action((key: string) => {
  const index = getTabIndex(key)
  tabs.splice(0, index)
  tabs.splice(1, tabs.length - 1)
  if (key !== uiSetting.curTabKey) {
    changeTab(key)
  }
})

const closeToTheRightTabs = action((key: string) => {
  tabs.splice(getTabIndex(key) + 1, tabs.length)
  if (key !== uiSetting.curTabKey) {
    changeTab(key)
  }
})

const closeSavedTabs = action(() => {
  tabs
    .filter((i) => !i.changed)
    .forEach((i) => {
      tabs.splice(getTabIndex(i.key), 1)
    })

  if (!tabs.map((i) => i.key).includes(uiSetting.curTabKey) && tabs.length > 0) {
    changeTab(tabs[0].key)
  }
})

const keepTabOpen = action((key: string) => {
  tabs.forEach((i) => {
    if (i.key === key) {
      i.tmp = false
    }
  })
})

const setTabChange = action((key: string, value: boolean) => {
  const tab = getTab(key)
  if (tab) {
    tab.changed = value
    if (value) {
      tab.tmp = false
    }
  }
})

export default {
  tabs,
  addTab,
  changeTab,
  closeTab,
  closeAllTabs,
  closeOtherTabs,
  closeToTheRightTabs,
  closeSavedTabs,
  keepTabOpen,
  getTab,
  setTabChange,
  curTab: computed(() => getTab(uiSetting.curTabKey)),
  uiSetting,
  toggleTheme,
  toggleWindowFooterVisible,
  changeWindowFooterSplitSize

  // 页面是否加载
  // tabPageLoaded,
  // setPageLoaded: action((tabKey: string, ok: boolean | undefined) => {
  //   tabPageLoaded[tabKey] = ok
  // })
}
