import request from '@/utils/request'
import {
  IBizData,
  IFileTreeData,
  IFile,
  IFileDetail,
  ModuleType,
  IGraphData,
  IOutputNode,
  ICommitRecord,
  IFolderParam,
  IFileParam,
  FileType
} from '../types/server'

export const fetchBizList = (workspaceId: number): Promise<IBizData[]> =>
  request.GET<IBizData[]>(`/biz/list?workspaceId=${workspaceId}`)

export const fetchTreeData = (
  workspaceId: number,
  belong: ModuleType,
  bizId: number
): Promise<IFileTreeData> =>
  request.GET<IFileTreeData>(`/tree?workspaceId=${workspaceId}&bizId=${bizId}&belong=${belong}`)

export const fetchFileDetail = (fileId: number): Promise<IFileDetail> =>
  request.GET<IFileDetail>(`/file?fileId=${fileId}`)

export const fetchDeletedFileList = (workspaceId: number): Promise<IFile[]> => {
  return request.GET<IFile[]>(`/file/deleteList?workspaceId=${workspaceId}`)
}

export const fetchBizGraph = (bizId: number): Promise<IGraphData<IFile>> => {
  return request.GET<IGraphData<IFile>>(`/biz/graph?bizId=${bizId}`)
}

export const fetchOutputNodes = (): Promise<IOutputNode[]> => {
  return request.GET<IOutputNode[]>(`/node/output`)
}
export const saveFile = (file: IFileDetail) => {
  return request.PUT<boolean>(`/file/detail`, file)
}

export const commitFile = (fileId: number, comment: string) => {
  return request.POST<boolean>(`/file/commit?`, { fileId, comment })
}

export function fetchCommitRecords(fileId: number) {
  return request.GET<ICommitRecord[]>(`/file/commit/list?fileId=${fileId}`)
}

export const createFolder = (param: IFolderParam) => {
  return request.POST<number>(`/folder`, param)
}

export const createFile = (param: IFileParam) => {
  return request.POST<IFile>('/file', param)
}

export const modifyFolder = (param: IFolderParam) => {
  return request.PUT<number>(`/folder`, param)
}

export const modifyFile = (param: IFileParam) => {
  return request.PUT<number>('/file', param)
}

export const getFiles = (
  workspaceId: number,
  bizId: number,
  belong: ModuleType,
  fileType?: FileType
) => {
  let url = `/file/list?workspaceId=${workspaceId}&bizId=${bizId}&belong=${belong}`
  if (fileType) {
    url += `&fileType=${fileType}`
  }
  return request.GET<IFile[]>(url)
}
