import React, { AnchorHTMLAttributes, ButtonHTMLAttributes } from 'react'
import './index.css'

import classNames from 'classnames'
import { BaseProps } from '@/pages/databench/types/client'

export type ButtonSize = 'lg' | 'sm'
export type ButtonColor = 'blue' | 'red' | 'green'

interface BaseButtonProps extends BaseProps {
  disabled?: boolean
  size?: ButtonSize
  color?: ButtonColor
  href?: string
  link?: boolean
}

type NativeButtonProps = BaseButtonProps & ButtonHTMLAttributes<HTMLElement>
type AnchorButtonProps = BaseButtonProps & AnchorHTMLAttributes<HTMLElement>
export type ButtonProps = Partial<NativeButtonProps & AnchorButtonProps>

const Button: React.FC<ButtonProps> = (props) => {
  const { className, size, color, href, link, disabled, children, ...restProps } = props

  const classes = classNames('btn', color ? 'color' : '', link ? 'link' : 'button', className, {
    [`btn-${color}`]: color,
    disabled,
    [`btn-${size}`]: size
  })

  return link ? (
    <a className={classes} href={href} {...restProps}>
      {children}
    </a>
  ) : (
    <button disabled={disabled} {...restProps} className={classes}>
      {children}
    </button>
  )
}

export default Button
