export interface LooseObject {
  [key: string]: any
}

export const newVal = <T = any>(t: T, consumer: (t: T) => void): T => {
  let i = { ...t }
  consumer(i)
  return i
}

export const loosertSetList = <T = any>(
  list: T[],
  predicate: (t: T) => boolean,
  consumer: (obj: LooseObject) => {}
): T[] => {
  return list.map((i) => {
    let j = i as unknown as LooseObject
    if (predicate(i)) {
      consumer(j)
      return j as T
    }
    return i as T
  })
}

export const rtSetList = <T = any>(
  list: T[],
  predicate: (t: T) => boolean,
  consumer: (t: T) => void
): T[] => {
  return list.map((t) => {
    if (predicate(t)) {
      consumer(t)
    }
    return t
  })
}

export const addRt = <T = any>(list: T[], t: T): T[] => {
  return list.concat([t])
}

export const addOrrtSetList = <T = any>(
  list: T[],
  t: T,
  predicate: (t: T) => boolean,
  consumer: (t: T) => void
): T[] => {
  const index = list.findIndex((i) => predicate(i))
  if (index === -1) {
    return list.concat([t])
  } else {
    return rtSetList(list, predicate, consumer)
  }
}

export const setLocal = <T = any>(key: string, value: T) => {
  localStorage.setItem(key, JSON.stringify(value))
}

export const getLocal = <T = any>(key: string, defaultValue: T) => {
  const value = localStorage.getItem(key)
  return value === undefined || value == null ? defaultValue : JSON.parse(value)
}

let count = 0
export const commonClick = (consumer: (double: boolean) => void) => {
  count += 1
  setTimeout(() => {
    // console.log('click', count)
    if (count > 0) {
      consumer(count == 2)
    }
    count = 0
  }, 200)
}
